/* é§’ã¯flexã®æ©Ÿèƒ½ã§5å€‹ã”ã¨ã«æŠ˜ã‚Šè¿”ã—ã€marginã§å°‘ã—ãšã¤ãšã‚‰ã—ã¦ã„ã‚‹ */
.column .point {
