    /* æŠ˜ã‚Šè¿”ã—ã®ãŸã‚ãšã‚‰ã™åˆ†ã®èª¿æ•´ */
    margin-left: calc(var(--pieceSize) * 7 / 8);
}

/* ä¸ŠåŠåˆ†ã§ã¯é§’ã‚’ä¸Šã‹ã‚‰ä¸‹ã€ä¸‹åŠåˆ†ã§ã¯ãã®é€† */
.upper .column .point {
    flex-direction: column;
