    /* margin-bottomã§pieceã®é‡ãªã‚Šã‚’èª¿æ•´ã™ã‚‹ã¨ã€ä¸‹æ®µã ã‘ä½™è¨ˆã«ãšã‚Œã‚‹ã®ã§èª¿æ•´ */
    margin-bottom: 1px;
