/* ã‚«ãƒ©ãƒ å†…ã€ã™ãªã‚ã¡ãƒã‚¤ãƒ³ãƒˆã¨ãƒãƒ¼ãƒã‚¤ãƒ³ãƒˆä¸Šã®é§’ */
.column .piece {
    width: var(--pieceSize);
    height: var(--pieceSize);
    border-radius: 50%;
    border: 1px solid black;

    /*ã»ã‹ã®ãƒ”ãƒ¼ã‚¹ã‚ˆã‚Šä¸‹*/
    z-index: 10;

    /* pieceã‚’æŠ˜ã‚Šè¿”ã—ã¦ã‚‚åŒã˜ä½ç½®ã«è¡¨ç¤ºã•ã›ã‚‹ */
    margin-left: calc(var(--pieceSize) * -7 / 8);

