}

.goal.upper .point {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.goal.lower .point {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
}

.goal .piece {
    width: var(--pieceSize);
    height: var(--pheight);
    border-radius: 5%;
    border: 1px solid black;
    margin: 1px 0;
}

/* 5å€‹ãŠãã«éš™é–“ã‚’é–‹ã‘ã‚‹ */
.goal.upper .piece:nth-child(5),
.goal.upper .piece:nth-child(10) {
