/*ãšã‚‰ã—ãŸã¨ãã€éš£ã®ãƒã‚¤ãƒ³ãƒˆä¸Šã®é§’ã‚ˆã‚Šä¸Šã«è¡¨ç¤ºã—ãŸã„ã®ã§ã€z-indexã§èª¿ç¯€ã™ã‚‹ */

.column .point .piece:nth-child(6),
.column .point .piece:nth-child(7),
