.board {
    margin: 5px;
    border-color: var(--boardColor);
    border-style: solid;
    border-width: calc(var(--pieceSize) * 0.5 + 3px)
        calc(var(--pieceSize) * 0.5);
    background-color: var(--boardBackgroundColor);
}

/* ã‚¤ãƒ³ãƒŠãƒ¼ãƒ»ã‚¢ã‚¦ã‚¿ãƒ¼ã¨ã€å·¦å³ã®ãƒãƒ¼ã¨ã®å¢ƒç•Œ */
.centerArea {
    border-color: var(--boardColor);
    border-style: solid;
    border-width: 0 calc(var(--pieceSize) * 0.5);
}

/* ä¸­å¤®ãƒ»å·¦å³ã®ãƒãƒ¼ */
.bar,
.side-bar {
    background-color: var(--boardColor);
}

/* ãƒ™ã‚¢ã‚ªãƒ•ã®é§’ã®ç½®ãå ´ */
.goal {
    background-color: lightgray;
}

/* åº§æ¨™è¡¨è¨˜ */
.posIndex {
    color: white;
    font-size: calc(var(--pieceSize) * 0.4);
}

/* é…è‰²ãƒ»ã‚µã‚¤ã‚ºãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿ãƒ¼ */
:root {
    --pieceSize: 32px;
    --diceSize: 36px;
    --dicePipSize: 70%;

    --boardColor: gray;
    --boardBackgroundColor: whitesmoke;

    --triangleColorEven: darkslategray;
    --triangleColorOdd: lightgray;

    --whiteSideColor: white;
    --whiteSideDicePipColor: black;

    --redSideColor: crimson;
    --redSideDicePipColor: white;

    --cubeColor: black;
    --cubeFontColor: white;
    --cubeMaxColor: darkgray;
    --cubeBorderColor: black;
}
