/* ãƒãƒƒã‚¯ã‚®ãƒ£ãƒ¢ãƒ³ãƒœãƒ¼ãƒ‰ã®åŸºæœ¬çš„ãªæ§‹æˆè¦ç´ ã‚’è¡¨ç¾ã™ã‚‹ã‚¹ã‚¿ã‚¤ãƒ«ã‚·ãƒ¼ãƒˆ */

:root {
    /* é…è‰²ãƒ»ã‚µã‚¤ã‚ºãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿ãƒ¼ */
    --boardHeight: calc(var(--pieceSize) * 17);
    --boardWidth: calc(var(--pieceSize) * 17);

    --redPieceColor: var(--redSideColor);
    --whitePieceColor: var(--whiteSideColor);
}

.boardContainer {
    width: fit-content;
    position: relative;
    display:flex;
    align-items: center;
}

.board {
    width: fit-content;

    /* ãƒ¬ã‚¤ã‚¢ã‚¦ãƒˆã¯åŸºæœ¬çš„ã«flexã§é…ç½® */
    display: flex;
    flex-flow: column;
}

.board * {
    /* boardå†…ã§ã¯borderè¾¼ã¿ã®ã‚µã‚¤ã‚ºã§ãƒ¬ã‚¤ã‚¢ã‚¦ãƒˆã‚’èª¿æ•´ã™ã‚‹ */
    box-sizing: border-box;
}

.board > div.innerBox {
    /* é«˜ã•ã¯ä»»æ„ã«èª¿æ•´ã§ãã‚‹ */
    height: var(--boardHeight);

    position: relative;
    display: flex;
    flex-flow: row;
}

/* åº§æ¨™è¡¨ç¤º */

.posIndex > span {
    text-align: center;
    width: calc(var(--pieceSize));
}

.posIndex {
    position: absolute;
    height: calc(var(--pieceSize) * 0.5);
    width: 100%;
    justify-content: space-around;
    display: flex;
}

.posIndex.upper {
    top: calc(var(--pieceSize) * -0.5);
    align-items: flex-end;
}

.posIndex.lower {
    bottom: calc(var(--pieceSize) * -0.5);
    align-items: flex-start;
}

/* ãƒœãƒ¼ãƒ‰ä¸­å¤® */

.centerArea {
    /* ãƒã‚¤ãƒ³ãƒˆã¨â–²â–³â–²â–³â–²â–³ã‚’çµ¶å¯¾åº§æ¨™ã§æŒ‡å®šã™ã‚‹ãŸã‚ */
    position: relative;

    display: flex;
}

/* ãƒœãƒ¼ãƒ‰ã®å³æ‰‹é¢ã€å·¦æ‰‹é¢ */
.inner,
.outer {
    margin: 0 2px;

    /* ç¸¦æ–¹å‘ã«ç›®ä¸€æ¯åºƒã’ã¦ table - dice - table ã¨é…ç½® */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    /* posIndexãŒAbsoluteã‚’ä½¿ç”¨ã™ã‚‹ã®ã§ */
    position: relative;
}

/* ãƒã‚¤ãƒ³ãƒˆ6å€‹åˆ†ã‚’è¡¨ç¤ºã™ã‚‹é ˜åŸŸ */
.table {
    position: relative;
}

/* ä¸ŠåŠåˆ†ã¯å³ã‹ã‚‰å·¦ã€ä¸‹åŠåˆ†ã¯ãã®é€†å‘ãã«ãƒã‚¤ãƒ³ãƒˆã‚’ä¸¦ã¹ã‚‹ */
.upper.table {
    display: flex;
    flex-direction: row;
}

.lower.table {
    display: flex;
    flex-direction: row-reverse;
}

/* ã‚«ãƒ©ãƒ ã¯ãƒã‚¤ãƒ³ãƒˆã¨â–²ã‚’ã¾ã¨ã‚ã¦ã„ã‚‹ã€‚ãƒãƒ¼ãƒã‚¤ãƒ³ãƒˆä¸Šã®é§’ã‚‚ã‚«ãƒ©ãƒ å†…ã«é…ç½® */
.column {
    width: var(--pieceSize);
}

.table .triangle {
    width: 0;
    height: 0;
    --triangle-height: 5.5;

    border-left: calc(var(--pieceSize) * 0.5) solid transparent;
    border-right: calc(var(--pieceSize) * 0.5) solid transparent;
}

.upper.table .triangle,
.upper.table .point {
    position: absolute;
    top: 0;
}

.lower.table .triangle,
.lower.table .point {
    position: absolute;
    bottom: 0;
}

/* â–¼â–½â–²â–³ã®è‰²åˆ†ã‘ */

.upper.table .column:nth-child(even) .triangle {
    border-top: calc(var(--pieceSize) * var(--triangle-height)) solid
        var(--triangleColorEven);
}

.upper.table .column:nth-child(odd) .triangle {
    border-top: calc(var(--pieceSize) * var(--triangle-height)) solid
        var(--triangleColorOdd);
}

.lower.table .column:nth-child(even) .triangle {
    border-bottom: calc(var(--pieceSize) * var(--triangle-height)) solid
        var(--triangleColorEven);
}

.lower.table .column:nth-child(odd) .triangle {
    border-bottom: calc(var(--pieceSize) * var(--triangle-height)) solid
        var(--triangleColorOdd);
}

/* å·¦å³ã€å„ãƒ—ãƒ¬ã‚¤ãƒ¤ãƒ¼ã®ãƒ€ã‚¤ã‚¹ */

.dice-space.left {
    left: 0;
    --diceColor: var(--redSideColor);
    --dicePipColor: var(--redSideDicePipColor);
}

.dice-space.right {
    right: 0;
    --diceColor: var(--whiteSideColor);
    --dicePipColor: var(--whiteSideDicePipColor);
}

/* ãƒãƒ¼ã«ã¯ã‚­ãƒ¥ãƒ¼ãƒ–ã¨ã‚ªãƒ³ã‚¶ãƒãƒ¼ã®é§’ç½®ãå ´ã‚’é…ç½® */

.bar {
    margin: 0 calc(var(--pieceSize) * 0.05);
    width: calc(var(--pieceSize) * 1.9);
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.bar .upper,
.bar .lower {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.bar .lower {
    flex-direction: column-reverse;
}

/* ã‚µã‚¤ãƒ‰ãƒãƒ¼ã«ã¯ãƒ™ã‚¢ã‚ªãƒ•ã—ãŸé§’ã®ç½®ãå ´ã¨ã€ãƒ€ãƒ–ãƒªãƒ³ã‚°ã‚­ãƒ¥ãƒ¼ãƒ– */

.side-bar {
    box-sizing: content-box;
    width: calc(var(--pieceSize) * 1 + 2px);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

/* goal é§’ã‚’ä¸Šå¯„ã›ã€ä¸‹å¯„ã›ã™ã‚‹ãŸã‚ã®æŒ‡å®š */

.goal {
    width: calc(var(--pieceSize) * 1 + 2px);
    height: calc(var(--pieceSize) * 0.25 * 17 + 28px);
}

.goal.upper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.goal.lower {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
}

.cube-space {
    align-self: center;

    display: flex;
    justify-content: center;
    align-items: center;
}
